  html {
    font-size: 8px;
  }

  #container-contacts-text-grid {
    padding-left: 2rem;
  }

  #container-contacts-text-grid span {
    font-size: 2rem;
  }

  /* BREADCRUMBS */
  #bcnl-container {
    grid-template-columns: 100%;
  }

  #breadcrumbs-links {
    display: none;
  }

  #bcnl-container a {
    font-size: 2.5rem;
  }

  #lang-container {
    grid-template-columns: 68% 16% 16%;
  }

  /* footer */
  #div-for-text span {
    font-size: 1.4rem;
  }
  footer p {
    font-size: 0.8rem;
  }
}


