#product-page-grid {
  display: grid;
  margin-top: 4.5rem;
  grid-template-columns: 28% 44% 28%;
}

#product-text {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;
  position: relative;
  top: 50%;
  font-family: "Roboto";
}
#main-product-img {
  width: auto;
  margin: 0 auto;
  text-align: center;
  justify-content: center;
  height: 25rem;
  max-width: 100%;
}

#prod-photo-container {
  margin: 0 auto;
  text-align: center;
}

.addit-prod-photo-container img {
  max-width: 14rem;
  height: auto;
  max-height: 12rem;
}

#product-name {
  font-size: 1.4rem;
}

#interior-container {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  width: 90%;
  margin: 0 auto;
}

#interior-container img {
  margin: 0 auto;
  margin-top: 0px;
  margin-top: 3rem;
  width: 28rem;
  height: 15.8rem;
  max-width: 100%;
  max-height: 100%;
  transform: scale(0.85);
}

#stickyFooter {
  position: fixed;
  bottom: 0;
  width: 100%;
  opacity: 0.7501;
  z-index: 9999;
}

